html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  max-width: 100%;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}
body.figar-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img, picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/* UI */
.no-webp .header-inicio-principal {
  background-image: url(../img/portadawebitsp.png);
}

.webp .header-inicio-principal {
  background-image: url(../img/portadawebitsp.webp);
}

.header {
  background-color: #e1e1e1;
  padding: 1rem 0 3rem 0;
}
.header.header-inicio-principal {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
}
.header.header-inicio-principal .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}
.header.header-inicio-principal .contenido-header h1 {
  text-align: left;
  color: #00066b;
  font-weight: 700;
  line-height: 1.5;
  max-width: 55rem;
}
.header img {
  width: 64px;
  height: 64px;
}

.footer {
  padding: 2.5rem;
  background-color: #202020;
  margin: 0;
}
.footer .footer-container {
  display: grid;
  gap: 3;
  grid-template-columns: repeat(2, 1fr);
  place-content: center;
  place-items: center;
}
.footer .footer-container .contenido-footer {
  padding-top: 2.5rem;
}
.footer .footer-container .contenido-footer nav a {
  color: #04c7d2;
}
.footer .footer-container .redes-sociales-footer .footer-rs-iconos {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(4, 1fr);
  text-align: center;
}
@media screen and (max-width: 768px) {
  .footer .footer-container .redes-sociales-footer .footer-rs-iconos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer .footer-container .redes-sociales-footer h3 {
  color: #ffffff;
  font-weight: 400;
}
.footer .footer-container .redes-sociales-footer a {
  color: #04c7d2;
}
.footer .footer-container .redes-sociales-footer a .fa-youtube {
  color: #c4302b;
}
.footer .footer-container .redes-sociales-footer a .fa-google-drive {
  color: #fbff00;
}
.footer .footer-container .redes-sociales-footer a .fa-facebook-square {
  color: #3b5998;
}
.footer .footer-container .redes-sociales-footer a i {
  color: #04c7d2;
  font-size: 3.5rem;
}
.footer .copyright {
  color: #ffffff;
  text-align: center;
}

.barra {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 2rem;
}
@media screen and (max-width: 768px) {
  .barra {
    flex-direction: column;
    padding-top: 0.5rem;
  }
}

.navegacion {
  display: flex;
  align-items: center;
}
.navegacion a {
  text-decoration: none;
  color: #00066b;
  font-size: 2rem;
  margin-right: 2rem;
}
.navegacion a:last-of-type {
  /*Seleciona el ultimo elemento y lo que vamos a hacer es eliminar el margin al ultimo elemento*/
  margin-right: 0;
}
@media screen and (max-width: 768px) {
  .navegacion a {
    display: block;
    text-align: center;
    margin: 0;
  }
}
@media screen and (max-width: 768px) {
  .navegacion {
    display: block;
    text-align: center;
  }
}

.main h1 {
  font-weight: 400;
}

.text-about-us {
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(2, 1fr);
  padding: 0 1rem 3rem 1rem;
  font-weight: 300;
  text-align: justify;
}
.text-about-us h3 {
  color: #00066b;
  font-weight: 400;
  text-transform: uppercase;
}
@media screen and (max-width: 768px) {
  .text-about-us {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
    padding: 0 3rem 2rem 3rem;
  }
}

.iconos-nosotros {
  display: grid;
  gap: 2;
  grid-template-columns: repeat(3, 1fr);
  place-content: center;
  text-align: center;
}
@media screen and (max-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2;
    grid-template-columns: repeat(1, 1fr);
  }
}

.icono {
  display: flex;
  flex-direction: column;
  place-items: center;
  width: 100%;
}
.icono img {
  height: 10rem;
  width: 10rem;
}
.icono h3 {
  color: #00066b;
  font-weight: 400;
  text-transform: uppercase;
}
.icono p {
  font-weight: 300;
}

.galeria h3 {
  font-weight: 400;
}

.galeria-img {
  margin: 0;
  padding: 0 0 3rem 0;
  width: 100%;
  height: 100%;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(4, 1fr);
}
@media screen and (max-width: 768px) {
  .galeria-img {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 480px) {
  .galeria-img {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.img-picture {
  border-radius: 50px;
}
.img-picture img {
  height: 120px;
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 2rem;
  display: grid;
  place-items: center;
  place-content: center;
  background-color: rgba(27, 27, 27, 0.733);
}
.overlay picture {
  display: block;
  max-width: 95%;
  max-height: 95%;
}
.overlay picture img {
  height: 100%;
  max-height: 100%;
}
.overlay .btn-close {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #ffffff;
  text-align: center;
  cursor: pointer;
  font-weight: 700;
  display: grid;
  place-items: center;
}